import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 目前正在播放的音乐的信息
    playingMusicObj: JSON.parse(localStorage.getItem('playingMusicObj')) || {
      // 正在播放的歌单
      songList: [],
      // 正在播放的歌曲
      playingSong: {
        id: undefined,
        // URL
        URL: undefined,
        // 总播放时长(xxx秒)
        totalTime: undefined,
        // 歌曲的图片
        coverURL: undefined,
        // 歌曲的名称
        songName: undefined,
        // 前缀
        prevName: undefined,
        // 后缀
        backName: undefined,
        // 是否高清
        sqMusic: null
      },
      // 当前的播放位置(xxx秒)
      playing: 0,
      // 是否播放
      isPlay: false
    },
    isShowPlayingList: false,
    // 1:单曲循环 2.列表循环 3.随机
    modeIndex: localStorage.getItem('modeIndex') || 1,
    searchHistory: JSON.parse(localStorage.getItem('searchHistory')) || [],
    // 点赞的评论的id
    likeComments: JSON.parse(localStorage.getItem('likeComments')) || [],
    // 正在播放的歌单（可编辑）
    playingSongList: JSON.parse(localStorage.getItem('playingSongList')) || []
  },
  getters: {

  },
  mutations: {
    setPlayingMusicObj: function (state, param) {
      state.playingMusicObj = param.value;
    },
    setIsShowPlayingList: function (state, param) {
      state.isShowPlayingList = param.value;
    },
    setModeIndex: function (state, param) {
      state.modeIndex = param.value;
      localStorage.setItem('modeIndex', param.value);
    },
    setSearchHistory: function (state, param) {
      state.searchHistory = param.value;
    },
    setLikeComments: function (state, param) {
      state.likeComments = param.value;
    },
    setPlayingSongList: function (state, param) {
      state.playingSongList = param.value;
    }
  },
  actions: {
  },
  modules: {
  }
});

